// import "@babel/polyfill";
import React from 'react';
import ReactDOM from 'react-dom';
import './index.less';
import App from './App';
import LoginModal from './component/LoginModal';
import Swiper from 'swiper';
import Bowser from "bowser";

import * as serviceWorker from './serviceWorker';

ReactDOM.render(<App />, document.getElementById('right-bar-container'));
ReactDOM.render(<LoginModal/>,document.getElementById('join-us'));

// 页面上图片轮播
/*var swiper = */ new Swiper('.first-page-container .swiper-container',{
    loop: true,
    autoplay: {
        delay: 3000,
        disableOnInteraction: false,
    },
});

/*var collaborator_swiper = */new Swiper('.collaborator .swiper-container',{
    loop: true,
    autoplay: {
        delay: 3000,
        disableOnInteraction: false,
    },
    navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev',
    },
});

// 浏览器检测
try{
    let browser = Bowser.getParser(window.navigator.userAgent).getBrowser();
    if(/Internet Explorer/i.test(browser.name)){
        if(Number.parseInt(browser.version)<=9){
            document.body.innerHTML = '不支持该IE版本，请使用IE10或者以上版本的IE浏览器';
        }
    }
    console.log('当前浏览器：',browser.name,'；版本：',browser.version);
}catch(e){
}

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
